  /* Hamburger */
  .hamburger1 {
    height: 10px;
    margin: 32px;
    display: -ms-grid;
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    justify-items: right;
    z-index: 120;
    margin-right: 10vw;
  }

  .hamburger1 div {
    background-color: rgb(61, 61, 61);
    justify-content: right;
    width: 23px;
    height: 1px;
    margin-top: 7px;
    transition: all 0.2s ease-in-out;
  }
  
  #toggle1 {
    display: none;
  }
  
  #toggle1:checked + .hamburger1 .top {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    margin-top: 22.5px;
  }
  
  #toggle1:checked + .hamburger1 .meat {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    margin-top: -0.5px;
  }
  
  #toggle1:checked + .hamburger1 .bottom {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  
  #toggle1:checked ~ .menu1 {
    height: 100%;
  }
  
  
  /* Menu */
  .menu1 {
    margin-left: 5vw;
    display: -ms-grid;
    margin-bottom: -70px;
    list-style: none;
    clear: both;
    width: auto;
    height: 0px;
    overflow: hidden;
    transition: height .4s ease;
    z-index: 120;
    transition: all 0.3s ease;
    justify-content: left;
    justify-items: left;
    float: left;
    position: relative;

  }

  
